<template>
  <div
    id="StoreInformation"
    class="table_box"
    style="   width: 100%;
    overflow: hidden; "
  >
    <div
      class="topOpt"
      style="background: rgba(255,255,255,.9);
    height: 42px;"
    >
      <el-button type="primary" size="small" @click="openAdd()" class="Btn"
        >新 增</el-button
      >
      <el-button
        type="primary"
        style="float: right; width: 150px; margin-right: 15vw"
        @click="search()"
        size="small"
        class="Btn"
        >搜 索</el-button
      >
      <input
        type="text"
        v-model="searchData"
        style="float: right;
    height: 32px;
    padding: 5px;
    margin: 5px 0 5px 10px;"
        @keyup.enter="search()"
      />
      <p style="float: right;font-size:14px;line-height:42px;">搜索一下</p>
      <el-button type="info" @click="openEdit()" size="small" class="Btn"
        >编 辑</el-button
      >
      <p
        style="font-size: 1.2rem; text-indent: 15vw;text-align:center;line-height: 42px;"
      >
        店铺信息汇总
      </p>
    </div>
    <el-table
      ref="SI_table"
      @row-click="rowClick"
      highlight-current-row
      max-height="770"
      border
      :data="tableList"
      style="width: 100%"
    >
      <el-table-column align="center" label="勾选" width="60">
        <template>
          <!-- @click="tag_check(scope.row.id)" -->
          <input type="checkbox" class="checkli" />
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        type="index"
        :index="indexMethod"
        label="序号"
        width="60"
      >
      </el-table-column>
      <el-table-column width="80" prop="platform" align="center" label="平台">
      </el-table-column>
      <el-table-column width="70" prop="site" align="center" label="站点">
      </el-table-column>
      <el-table-column width="70" prop="country" align="center" label="国家">
      </el-table-column>
      <el-table-column width="70" prop="type" align="center" label="大类">
      </el-table-column>
      <el-table-column
        prop="name_shop"
        align="center"
        label="店铺名称"
        width="100"
      >
      </el-table-column>
      <el-table-column prop="state" align="center" label="状态" width="80">
      </el-table-column>
      <el-table-column
        prop="order_number"
        width="230"
        align="center"
        label="主IP"
      >
        <template slot-scope="scope">
          <span
            class="singleIp"
            v-for="(item, index) in scope.row.host_ip_arr"
            :key="index"
          >
            {{ item }}
          </span>

          <!-- <el-input
            readonly
            :rows="2"
            title="右滑查看或者复制全部内容"
            type="textarea"
            v-model="scope.row.host_ip"
          ></el-input> -->
        </template>
      </el-table-column>
      <el-table-column prop="next_ip" align="center" label="子IP" width="180">
        <template slot-scope="scope">
          <span
            class="singleIp"
            v-for="(item, index) in scope.row.next_ip_arr"
            :key="index"
          >
            {{ item }}
          </span>
          <!-- <el-input
            :rows="2"
            readonly
            title="右滑查看或者复制全部内容"
            type="textarea"
            v-model="scope.row.next_ip"
          ></el-input> -->
        </template>
      </el-table-column>
      <el-table-column width="160" prop="email" align="center" label="店铺邮箱">
      </el-table-column>
      <el-table-column
        align="center"
        prop="company"
        label="法定公司名称"
        width="230"
      >
      </el-table-column>
      <el-table-column
        align="center"
        prop="address"
        width="200"
        label="公司地址"
      >
      </el-table-column>
      <el-table-column align="center" prop="phone" width="120" label="手机号码">
      </el-table-column>
      <el-table-column
        align="center"
        prop="phone_name"
        width="125"
        label="联系人"
      >
      </el-table-column>
      <el-table-column align="center" prop="currency" width="80" label="币种">
      </el-table-column>
    </el-table>
    <el-pagination
      background
      @current-change="handleCurrentChange"
      :current-page="page"
      :page-size="50"
      layout="total,prev, pager, next, jumper"
      :total="total"
      style="margin:10px auto 0;width:600px;background:#fff;text-align:center"
    >
    </el-pagination>
    <!-- 新增弹框 -->
    <el-dialog
      title="新增店铺"
      :visible.sync="centerDialogVisible"
      width="360px"
      :close-on-click-modal="false"
      center
      @close="closeAddDialog"
    >
      <span class="dialogSpan1">
        <span style="position: absolute; top: 10.5vh">渠道：</span>
        <span style="position: absolute; top: 16vh">国家：</span>
        <span style="position: absolute; top: 21.5vh">站点：</span>
        <span style="position: absolute; top: 27vh">大类：</span>
        <div class="add">
          <div style="positive: ">
            <el-col :span="18">
              <el-autocomplete
                class="inline-input"
                v-model="addDitch"
                :fetch-suggestions="querySearch"
                placeholder="请输入内容"
              ></el-autocomplete>
            </el-col>
          </div>
          <div>
            <el-col :span="18">
              <el-autocomplete
                class="inline-input"
                v-model="addCountry"
                :fetch-suggestions="querySearch2"
                placeholder="请输入内容"
              ></el-autocomplete>
            </el-col>
          </div>
          <div>
            <el-col :span="18">
              <el-autocomplete
                class="inline-input"
                v-model="addSite"
                :fetch-suggestions="querySearch3"
                placeholder="请输入内容"
              ></el-autocomplete>
            </el-col>
          </div>
          <div>
            <el-select
              v-model="addType"
              filterable
              placeholder="请选择"
              style="width: 202px; margin: 10px 0 0 100px"
            >
              <el-option
                v-for="item in addTypeList"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </div>
          店铺名称:
          <input
            type="text"
            v-model="addShopName"
            style="margin-left: 37px;
    border: 1px solid #DCDFE6;
    border-radius: 4px;
    width: 202px;
    padding-left: 15px;"
            @keyup.enter="addSubmit"
          />
          <br />
          状态:
          <el-select
            class="selectOpt"
            v-model="addStatus"
            @keyup.enter="addSubmit"
          >
            <el-option
              v-for="(item, index) in addStatusList"
              :key="index"
              :value="item.value"
            ></el-option>
          </el-select>

          <!-- <select
            name=""
            id=""
            v-model="addStatus"
            style="margin-left: 66px;
    border: 1px solid #DCDFE6;
    border-radius: 4px;
    width: 202px;
    padding-left: 15px;outline: none;"
            @keyup.enter="addSubmit"
          >
            <option
              v-for="(item, index) in addStatusList"
              :key="index"
              :value="item.value"
            >
              {{ item.value }}
            </option>
          </select> -->
          <div>
            店铺邮箱:<input
              type="text"
              v-model="addShopMessage"
              style="margin-left: 43px;
    border: 1px solid #DCDFE6;
    border-radius: 4px;
    width: 202px;
    padding-left: 15px;"
              @keyup.enter="addSubmit"
            />
          </div>
          <div>
            法定公司名称:<input
              type="text"
              v-model="addLegalCompanyName"
              @keyup.enter="addSubmit"
              style="margin-left: 16px;
    border: 1px solid #DCDFE6;
    border-radius: 4px;
    width: 202px;
    padding-left: 15px;"
            />
          </div>
          <div>
            公司地址:
            <input
              type="text"
              v-model="addCompanyAddress"
              style="margin-left: 41px;
    border: 1px solid #DCDFE6;
    border-radius: 4px;
    width: 202px;
    padding-left: 15px;"
              @keyup.enter="addSubmit"
            />
          </div>
          <div>
            联系人:
            <input
              type="text"
              v-model="addPeople"
              style="margin-left: 55px;
    border: 1px solid #DCDFE6;
    border-radius: 4px;
    width: 202px;
    padding-left: 15px;"
              @keyup.enter="addSubmit"
            />
          </div>
          <div>
            手机号码:
            <input
              type="text"
              v-model="addPhone"
              style="margin-left: 41px;
    border: 1px solid #DCDFE6;
    border-radius: 4px;
    width: 202px;
    padding-left: 15px;"
              @keyup.enter="addSubmit"
            />
          </div>
          <div>
            币种:
            <input
              type="text"
              v-model="addCurrency"
              style="margin-left: 69px;
    border: 1px solid #DCDFE6;
    border-radius: 4px;
    width: 202px;
    padding-left: 15px;"
              @keyup.enter="addSubmit"
            />
          </div>
        </div>

        <!-- <br />主IP <i class="el-icon-circle-plus-outline" @click="addIP()"></i>
        <i class="el-icon-remove-outline" @click="removeIP()"></i>
        <div class="firstIp">
          <input type="text" style="margin: 0" @keyup.enter="addSubmit" />
        </div> -->
        <div
          style="position: relative;
    line-height: 40px;
    margin: 10px 0;"
        >
          主IP <i class="el-icon-circle-plus-outline" @click="addIP()"></i>
          <i class="el-icon-remove-outline" @click="removeIP()"></i>
          <div class="firstIp">
            <input
              type="text"
              v-for="(item, index) in MainIpList"
              :key="index"
              style="margin: 0"
              v-model="item.ip"
              @keyup.enter="addSubmit"
            />
            <!-- <input type="text" style="margin: 0" @keyup.enter="addSubmit" /> -->
          </div>
        </div>

        <!-- <br />
        子IP <i class="el-icon-circle-plus-outline" @click="addIP2()"></i>
        <i class="el-icon-remove-outline" @click="removeIP2()"></i>
        <div class="twiceIp">
          <input type="text" style="margin: 0" @keyup.enter="addSubmit" />
        </div> -->
        <div
          style="position: relative;
    line-height: 40px;
    margin: 10px 0;"
        >
          子IP <i class="el-icon-circle-plus-outline" @click="addIP2()"></i>
          <i class="el-icon-remove-outline" @click="removeIP2()"></i>
          <div class="twiceIp">
            <input
              type="text"
              v-for="(item, index) in smallIpList"
              :key="index"
              style="margin: 0"
              v-model="item.ip"
              @keyup.enter="addSubmit"
            />
            <!-- <input type="text" style="margin: 0" @keyup.enter="addSubmit" /> -->
          </div>
        </div>
      </span>
      <div style="text-align:center;margin-top:5px;">
        <el-button type="info" @click="centerDialogVisible = false"
          >取 消</el-button
        >
        <el-button type="warning" @click="reset()">重 置</el-button>
        <el-button type="primary" @click="addSubmit()">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 编辑弹框 -->
    <el-dialog
      title="编辑店铺"
      :visible.sync="centerDialogVisible2"
      :close-on-click-modal="false"
      width="400px"
      center
      class="edit_log"
    >
      <span class="dialogSpan">
        <div class="dialogSpanLeft">
          <div>渠道</div>
          <div>站点</div>
          <div>国家</div>
          <div>大类</div>
          <div>店铺名称</div>
          <div>状态</div>
          <div>
            主IP<i class="el-icon-circle-plus-outline" @click="editIP()"></i>
            <i class="el-icon-remove-outline" @click="removeEditIP()"></i>
          </div>
          <div>
            子IP<i class="el-icon-circle-plus-outline" @click="editIP2()"></i>
            <i class="el-icon-remove-outline" @click="removeEditIP2()"></i>
          </div>
          <div>店铺邮箱</div>
          <div>法定公司名称</div>
          <div>公司地址</div>
          <div>联系人</div>
          <div>手机号码</div>
          <div>币种</div>
        </div>
        <div class="readonlyDiv">
          <input type="text" v-model="editDitch" readonly />

          <input type="text" readonly v-model="editSite" />

          <input type="text" readonly v-model="editCountry" />

          <input type="text" v-model="editType" readonly />

          <input type="text" readonly v-model="editShopName" />
          <el-select class="editStatus" v-model="editStatus">
            <el-option
              v-for="(item, index) in addStatusList"
              :key="index"
              :value="item.value"
              :label="item.label"
            ></el-option>
          </el-select>
          <!-- <select v-model="editStatus">
            <option
              v-for="(item, index) in addStatusList"
              :key="index"
              :value="item.value"
            >
              {{ item.value }}
            </option>
          </select> -->
          <div class="edit_IP_div">
            <!-- :value="item" -->
            <input
              type="text"
              v-for="(item, index) in edit_IP"
              v-model="item.ip"
              :key="index"
              style="width: 202px;
    height: 40px;
    border-radius: 5px;
    border: 1px solid #e5e5e5;
    outline: none;
    margin: 0 0 0 10px;"
              @keyup.enter="edit_submit"
            />
          </div>
          <div class="edit_nextIP_div">
            <!-- :value="item" -->
            <input
              type="text"
              v-for="(item, index) in edit_nextIP"
              v-model="item.ip"
              :key="index"
              style="width: 202px;
    height: 40px;
    border-radius: 5px;
    border: 1px solid #e5e5e5;
    outline: none;
    margin: 0 0 0 10px;"
              @keyup.enter="edit_submit"
            />
          </div>
          <input type="text" readonly v-model="editEmial" />
          <input
            type="text"
            v-model="editLegalCompanyName"
            @keyup.enter="edit_submit"
          />
          <input
            type="text"
            v-model="editCompanyAddress"
            @keyup.enter="edit_submit"
          />
          <input type="text" v-model="editPeople" @keyup.enter="edit_submit" />
          <input type="text" v-model="editPhone" @keyup.enter="edit_submit" />
          <input
            type="text"
            v-model="editCurrency"
            @keyup.enter="edit_submit"
          />
        </div>
      </span>
      <div style="text-align:center;margin-top:10px;">
        <el-button @click="closeEdit()" type="info">取 消</el-button>
        <el-button type="primary" @click="edit_submit()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      tableList: [], //页面表格数据
      addDitchList: [], //新增页面渠道下拉
      addCountryList: [], //新增页面国家下拉
      addTypeList: [], //新增页面大类下拉
      addSiteList: [], //新增页面站点下拉
      addDitch: "", //新增页面渠道
      addCountry: "", //新增页面国家
      addType: "", //新增页面大类
      addSite: "", //新增页面站点
      addShopName: "", //新增店铺名称
      addStatus: "", //新增界面状态
      addStatusList: [], //新增界面状态下拉
      addShopMessage: "", //新增店铺邮箱
      addLegalCompanyName: "", //新增法定公司名称
      addCompanyAddress: "", //新增公司地址
      addPeople: "", // 新增联系人
      addPhone: "", // 新增电话号码
      addCurrency: "", //新增币种
      MainIpList: [{ ip: "" }], // 主ip
      smallIpList: [{ ip: "" }], // 次ip
      centerDialogVisible: false,
      centerDialogVisible2: false,
      // centerDialogVisible2: true,

      restaurants: [],
      restaurants2: [],
      restaurants3: [],
      checkId: "", //选中复选框ID
      editDitch: "", //编辑渠道
      editSite: "", //编辑界面站点
      editCountry: "", //编辑界面国家
      editType: "", //编辑界面大类
      editShopName: "", //编辑界面店铺名称
      editStatus: "", //编辑界面状态
      edit_IP: [], //编辑界面主IP
      edit_nextIP: [], //编辑界面子IP
      editEmial: "", //编辑界面店铺邮箱
      editLegalCompanyName: "", //编辑界面法定公司名称
      editCompanyAddress: "", //编辑界面公司地址
      editPeople: "", // 编辑联系人
      editPhone: "", // 编辑手机号码
      editCurrency: "", //编辑
      total: 0, //总页数
      page: 1, //页码当前页

      flag: 0, //新增界面判断

      searchData: "", //搜索的关键字
      addDialogFlag: false,
      curSelectTarget: "" //当前选中对象
    };
  },
  methods: {
    indexMethod(index) {
      return index + 1 + (this.page - 1) * 50;
    },
    // 验证手机号码或者座机
    isPhone(numbers) {
      return /^((13[0-9])|(14[5,7,9])|(15[^4])|(18[0-9])|(17[0,1,3,5,6,7,8]))[0-9]{8}$/.test(
        numbers
      );
    },
    querySearch(queryString, cb) {
      var restaurants = this.restaurants;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) {
      return restaurant => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },
    querySearch2(queryString, cb) {
      var restaurants2 = this.restaurants2;
      var results = queryString
        ? restaurants2.filter(this.createFilter(queryString))
        : restaurants2;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) {
      return restaurant2 => {
        return (
          restaurant2.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },
    querySearch3(queryString, cb) {
      var restaurants3 = this.restaurants3;
      var results = queryString
        ? restaurants3.filter(this.createFilter(queryString))
        : restaurants3;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) {
      return restaurant3 => {
        return (
          restaurant3.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },
    getDefalutData() {
      //获取默认数据 如进入页面表格
      axios({
        url: this._url2 + "server/databases/store_information",
        method: "get",
        params: {
          page: this.page,
          condition: this.searchData
        }
      }).then(res => {
        console.log(res.data);
        // this.total = res.data.count_data[0].count_id / 5;
        this.total = res.data.count_data[0].count_id;
        this.tableList = res.data.data;
        // 切割ip ip分别放入 next_ip_arr  host_ip_arr
        this.$nextTick(() => {
          this.$refs["SI_table"].bodyWrapper.scrollTop = 0;
        });
        for (let key in this.tableList) {
          let item = this.tableList[key];
          this.$set(item, "host_ip_arr", item.host_ip.split("*"));
          this.$set(item, "next_ip_arr", item.next_ip.split("*"));
          //   let item = this.tableList[key];
          //   let arr = item.host_ip.split("*");
          //   let len = arr.length - 1;
          //   if (arr.length > 0) {
          //     for (let key in arr) {
          //       if (arr[len] !== "" && key != len) {
          //         console.log(key, len, arr);
          //         arr[key] += ",";
          //       }
          //     }
          //   }
          //   let brr = item.next_ip.split("*");
          //   let lenb = brr.length - 1;
          //   if (brr.length > 0) {
          //     for (let key in brr) {
          //       if (brr[len] !== "" && key != lenb) {
          //         brr[key] += ",";
          //       }
          //     }
          //   }
          //   this.$set(item, "host_ip_arr", arr);
          //   this.$set(item, "next_ip_arr", brr);
        }

        this.addDitchList = res.data.channel_data;
        this.addCountryList = res.data.country_data;
        this.addTypeList = res.data.type_data;
        this.addSiteList = res.data.site_date;
        this.addStatusList = res.data.store_state_data;
        for (let i = 0; i < this.addDitchList.length; i++) {
          delete this.addDitchList[i].label;
          console.log(this.addDitchList);
        }
        this.restaurants = this.addDitchList;
        this.restaurants2 = this.addCountryList;
        this.restaurants3 = this.addSiteList;
      });
    },
    search() {
      //搜索页面数据
      axios({
        url: this._url2 + "server/databases/store_information",
        method: "get",
        params: {
          page: 1,
          condition: this.searchData
        }
      }).then(res => {
        // this.total = res.data.count_data[0].count_id / 5;
        this.total = res.data.count_data[0].count_id;
        this.tableList = res.data.data;
        this.page = 1;
        console.log(res);
      });
    },
    resetAddIP() {
      // let inputNodes = document.querySelector(".firstIp").children;
      // let inputNodes2 = document.querySelector(".twiceIp").children;
      // if (inputNodes.length === 0) {
      //   let str = document.createElement("input");
      //   str.style.width = "202px";
      //   str.style.display = "block";
      //   str.style.height = "40px";
      //   str.style.border = "1px solid #dcdfe6";
      //   str.style.borderRadius = "5px";
      //   str.style.paddingLeft = "15px";
      //   document.querySelector(".firstIp").appendChild(str);
      // }
      // if (inputNodes2.length === 0) {
      //   let str = document.createElement("input");
      //   str.style.width = "202px";
      //   str.style.display = "block";
      //   str.style.height = "40px";
      //   str.style.border = "1px solid #dcdfe6";
      //   str.style.borderRadius = "5px";
      //   str.style.paddingLeft = "15px";
      //   document.querySelector(".twiceIp").appendChild(str);
