    reset() {
      //重置按钮
      this.centerDialogVisible = true;
      this.addDitch = "";
      this.addCountry = "";
      this.addSite = "";
      this.addType = "";
      this.addShopName = "";
      this.addStatus = "";
      this.addShopMessage = "";
      this.addLegalCompanyName = "";
      this.addCompanyAddress = "";
      this.addPeople = "";
      this.addPhone = "";
      this.addCurrency = "";

      this.resetAddIP();

      let inputLI = document
        .querySelector(".firstIp")
        .querySelectorAll("input");
      let inputLI2 = document
        .querySelector(".twiceIp")
        .querySelectorAll("input");
      for (let i = 0; i < inputLI.length; i++) {
        inputLI[i].value = "";
      }
      for (let i = 0; i < inputLI2.length; i++) {
        inputLI2[i].value = "";
      }
    },
    addSubmit() {
      this.addDialogFlag = true;

      //增加操作
      let checkcountry = this.checkeChinese(this.addCountry);
      let checksite = this.checkeChinese(this.addSite);
      let checkPLa = this.checkeEnglist(this.addDitch);
      let flag = 0;
      let flag2 = 0;
      // 拿到所有子元素
      let inputLI = document
        .querySelector(".firstIp")
        .querySelectorAll("input");
      let inputLI2 = document
        .querySelector(".twiceIp")
        .querySelectorAll("input");
      for (let i = 0; i < inputLI.length; i++) {
        // 不是ip flag加一
        if (this.isIP(inputLI[i].value) === false) {
          flag++;
        }
      }
      for (let i = 0; i < inputLI2.length; i++) {
        if (this.isIP(inputLI2[i].value) === false) {
          // 不是ip，flag加一
          flag2++;
        }
      }
      // 验证手机号码或者座机
      if (this.addPhone && !this.isPhone(this.addPhone)) {
        alert("请输入正确的手机号");
        return;
      }

      if (
        this.addShopName == "" ||
        this.addSite == "" ||
        this.addCountry == "" ||
        this.addDitch == "" ||
        this.addStatus == "" ||
        this.addShopMessage == "" ||
        this.addLegalCompanyName == "" ||
        this.addCompanyAddress == "" ||
        this.addType == "" ||
        this.addCurrency == "" ||
        checkcountry == false ||
        checksite == false ||
        checkPLa == false ||
        flag != 0 ||
        flag2 != 0
      ) {
        alert(
          "请把输入所有输入框（站点，国家只能是中文，渠道只能是英文,IP地址和子IP格式例子192.168.1.1，可删除为空，联系人及手机号码可不填）"
        );
      } else {
        this.centerDialogVisible = false;
        let Ip1 = "";
        let Ip2 = "";
        for (let i = 0; i < inputLI.length; i++) {
          Ip1 += inputLI[i].value + "*";
        }
        for (let i = 0; i < inputLI2.length; i++) {
          Ip2 += inputLI2[i].value + "*";
        }
        Ip1 = Ip1.substring(0, Ip1.length - 1);
        Ip2 = Ip2.substring(0, Ip2.length - 1);
        let formData = new FormData();
        formData.append("name_shop", this.addShopName);
        formData.append("site", this.addSite);
        formData.append("country", this.addCountry);
        formData.append("platform", this.addDitch);
        formData.append("state", this.addStatus);
        formData.append("email", this.addShopMessage);
        formData.append("company", this.addLegalCompanyName);
        formData.append("address", this.addCompanyAddress);
        formData.append("type", this.addType);
        formData.append("currency", this.addCurrency);
        // 传新值
        formData.append("phone_name", this.addPeople);
        formData.append("phone", this.addPhone);

        formData.append("host_ip", Ip1);
        formData.append("next_ip", Ip2);
        this.$axios
          .post(this._url2 + "server/databases/store_information", formData)
          .then(res => {
            if (res.data.code == 200) {
              alert("新增成功");
              // 新增后直接跳到最后一页
              console.log(this.total);
              // 翻页
              let lastPage = Math.ceil((this.total + 1) / 50);
              this.page = lastPage;
              this.getDefalutData();
            }
          });
      }
    },
    edit_submit() {
      //编辑操作
      let flag = 0;
      let flag2 = 0;
      let inputLI = document
        .querySelector(".edit_IP_div")
        .querySelectorAll("input");
      let inputLI2 = document
        .querySelector(".edit_nextIP_div")
        .querySelectorAll("input");
      for (let i = 0; i < inputLI.length; i++) {
        if (this.isIP(inputLI[i].value) === false) {
          flag++;
        }
      }
      for (let i = 0; i < inputLI2.length; i++) {
        if (this.isIP(inputLI2[i].value) === false) {
          flag2++;
        }
      }
      if (this.editPhone && !this.isPhone(this.editPhone)) {
        alert("请输入正确的手机号码");
        return;
      }
      if (
        this.editShopName == "" ||
        this.editSite == "" ||
        this.editCountry == "" ||
        this.editDitch == "" ||
        this.editStatus == "" ||
        this.editShopMessage == "" ||
        this.editLegalCompanyName == "" ||
        this.editCompanyAddress == "" ||
        this.editType == "" ||
        this.editCurrency == "" ||
        flag != 0 ||
        flag2 != 0
      ) {
        alert(
          '请输入除"IP、联系人、手机号码"以外所有的输入框（IP地址和子IP格式例子192.168.1.1，可删除为空）'
        );
      } else {
        let Ip1 = "";
        let Ip2 = "";
        for (let i = 0; i < inputLI.length; i++) {
          Ip1 += inputLI[i].value + "*";
        }
        for (let i = 0; i < inputLI2.length; i++) {
          Ip2 += inputLI2[i].value + "*";
        }
        Ip1 = Ip1.substring(0, Ip1.length - 1); //消除最后的*号
        Ip2 = Ip2.substring(0, Ip2.length - 1); //消除最后的*号
        if (this.editCompanyAddress == null) {
          this.editCompanyAddress = "";
        }
        if (this.editLegalCompanyName == null) {
          this.editLegalCompanyName = "";
        }
        let formData = new FormData();
        formData.append("id", this.checkId);
        formData.append("address", this.editCompanyAddress);
        formData.append("company", this.editLegalCompanyName);
        formData.append("state", this.editStatus);
        formData.append("type", this.editType);
        formData.append("host_ip", Ip1);
        formData.append("next_ip", Ip2);
        formData.append("phone_name", this.editPeople);
        formData.append("phone", this.editPhone);
        formData.append("currency", this.editCurrency);
        this.$axios
          .put(this._url2 + "server/databases/store_information", formData)
          .then(res => {
            if (res.data.code == 200) {
              this.getDefalutData();
              alert("编辑成功");
            }
          });
        this.centerDialogVisible2 = false;
      }
    },
    rowClick(row) {
      console.log(row.id);
      let id = row.id;
      this.checkId = id;
      let index = this.tableList.findIndex(i => i.id === id) + 1;

      let checklist = document.querySelectorAll(".checkli");

      if (this.curSelectTarget) {
        // 选中状态改为false
        this.curSelectTarget.checked = false;
      }
      console.log(this.curSelectTarget);
      if (this.curSelectTarget !== checklist[index]) {
        // 不是选中同一个

        checklist[index].checked = true;
        this.curSelectTarget = checklist[index];
      } else {
        this.curSelectTarget = "";
        this.checkId = "";
      }
    },
