    trTag_check(index, id) {
      //点击勾选框
      this.checkId = id;
      let checklist = document.querySelectorAll(".checkli");
      if (checklist[index].checked === true) {
        for (let i = 0; i < checklist.length; i++) {
          checklist[i].checked = false;
        }
        this.checkId = "";
      } else {
        for (let i = 0; i < checklist.length; i++) {
          checklist[i].checked = false;
        }
        checklist[index].checked = true;
      }
    },
    openEdit() {
      //判断勾选项
      if (this.checkId == "") {
        alert("请选择要编辑的选项");
      } else {
        // this.centerDialogVisible2 = true;
        this.edit_IP = [];
        this.edit_nextIP = [];

        console.log(this.tableList);
        for (let i = 0; i < this.tableList.length; i++) {
          if (this.checkId === this.tableList[i].id) {
            console.log("打印表格行", this.tableList[i]);
            this.editDitch = this.tableList[i].platform;
            this.editSite = this.tableList[i].site;
            this.editCountry = this.tableList[i].country;
            this.editShopName = this.tableList[i].name_shop;
            this.editEmial = this.tableList[i].email;
            this.editCurrency = this.tableList[i].currency;
            // 手机号，联系人
            //     formData.append("phone_name", this.editPeople);
            // formData.append("phone", this.editPhone);
            this.editPhone = this.tableList[i].phone;
            this.editPeople = this.tableList[i].phone_name;

            if (this.tableList[i].host_ip) {
              // this.edit_IP = this.tableList[i].host_ip.split("*");
              let editArr = this.tableList[i].host_ip.split("*");
              for (let key in editArr) {
                this.$set(this.edit_IP, key, {});
                console.log(key, this.edit_IP);
                this.$set(this.edit_IP[key], "ip", editArr[key]);
              }
              console.log("主IP", this.edit_IP, this.tableList[i].host_ip);
            }
            if (this.tableList[i].next_ip) {
              // this.edit_nextIP = this.tableList[i].next_ip.split("*");
              let editArr = this.tableList[i].next_ip.split("*");
              for (let key in editArr) {
                this.$set(this.edit_nextIP, key, {});
                console.log(key, this.edit_nextIP);
                this.$set(this.edit_nextIP[key], "ip", editArr[key]);
              }
              console.log("IP", this.edit_nextIP, this.tableList[i].next_ip);
            }
            this.editLegalCompanyName = this.tableList[i].company;
            this.editCompanyAddress = this.tableList[i].address;
            this.editType = this.tableList[i].type;
            this.editStatus = this.tableList[i].state;
          }
          this.centerDialogVisible2 = true;
        }
      }
    },
    closeEdit() {
      this.centerDialogVisible2 = false;
    },
    handleCurrentChange(newPage) {
      // 页码切换会触发此函数
      this.page = newPage;
      this.getDefalutData();
      let checkli = document.querySelectorAll(".checkli");
      for (let i = 0; i < checkli.length; i++) {
        checkli[i].checked = false;
      }
    },
    checkeChinese(str) {
      //中文正则\
      let demo = str.match(/[^\u4e00-\u9fa5]/g);
      if (demo) {
        return false;
      } else {
        return true;
      }
    },
    checkeEnglist(str) {
      //英文正则
      let demo = str.match(/[^A-Za-z]/g);
      if (demo) {
        return false;
      } else {
        return true;
      }
    },
    isIP(strIP) {
      //IP地址正则
      // if (isNull(strIP)) return false;
      var re = /^(\d+)\.(\d+)\.(\d+)\.(\d+)$/g; //匹配IP地址的正则表达式
      if (re.test(strIP)) {
        if (
          RegExp.$1 < 256 &&
          RegExp.$2 < 256 &&
          RegExp.$3 < 256 &&
          RegExp.$4 < 256
        )
          return true;
      }
      return false;
    }
  },
  created() {
    this.getDefalutData();
  }
};
