  background-color: rgba(236, 230, 230, 0.9);
} */
/* 设置滚动条的大小 */
::-webkit-scrollbar {
  width: 5px;
  height: 9px;
}
::-webkit-resize {
  display: none;
}
::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #dddddd;
  opacity: 0.7;
  background-clip: padding-box;
  min-height: 5vh;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #bbb;
}
</style>



